<template>
  <div class="register">
    <div
      style="
        margin: 100px auto;
        background-color: #fff;
        width: 350px;
        height: 400px;
        padding: 20px;
        border-radius: 10px;
      "
    >
      <div style="margin: 20px 0; text-align: center; font-size: 24px">
        <b>注 册</b>
      </div>
      <el-form :model="user" :rules="rules" ref="userForm">
        <el-form-item prop="username">
          <el-input
            placeholder="请输入账号"
            size="medium"
            style="margin: 3px 0"
            prefix-icon="el-icon-user"
            v-model="user.username"
          ></el-input>
        </el-form-item>
        <el-form-item prop="password">
          <el-input
            placeholder="请输入密码"
            size="medium"
            style="margin: 3px 0"
            prefix-icon="el-icon-lock"
            show-password
            v-model="user.password"
          ></el-input>
        </el-form-item>
        <el-form-item prop="confirmPassword">
          <el-input
            placeholder="请确认密码"
            size="medium"
            style="margin: 3px 0"
            prefix-icon="el-icon-lock"
            show-password
            v-model="user.confirmPassword"
          ></el-input>
        </el-form-item>
        <el-form-item style="margin: 3px auto; text-align: center">
          <VerificationCode @checkIDCode="check"></VerificationCode>
        </el-form-item>
        <el-form-item style="margin: 10px auto; text-align: center">
          <el-button
            type="primary"
            size="medium"
            autocomplete="off"
            @click="login"
            >注册</el-button
          >
          <el-button
            type="warning"
            size="medium"
            autocomplete="off"
            @click="$router.push('/login')"
            >返回登录</el-button
          >
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>

<script>
import VerificationCode from "@/components/utils/VerificationCode.vue";
export default {
  components: { VerificationCode },
  name: "Register",

  data() {
    return {
      flag: false,
      user: {},
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          { min: 3, max: 10, message: "长度在 3 到 5 个字符", trigger: "blur" },
        ],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 8,
            max: 16,
            message: "长度在 8 到 16 个字符",
            trigger: "blur",
          },
        ],
        confirmPassword: [
          { required: true, message: "请输入密码", trigger: "blur" },
          {
            min: 8,
            max: 16,
            message: "长度在 8 到 16 个字符",
            trigger: "blur",
          },
        ],
      },
    };
  },

  components: {
    VerificationCode,
  },

  mounted() {},

  methods: {
    login() {
      this.$refs["userForm"].validate((valid) => {
        if (valid) {
          // 表单校验合法
          if (this.user.password !== this.user.confirmPassword) {
            this.$message.error("两次输入的密码不一致");
            return false;
          }
          if (this.flag) {
            this.$axios.post("/register", this.user).then((res) => {
              if (res.data.code == "200") {
                this.$message.success("注册成功");
                this.$router.push("/login");
              } else {
                this.$message.error(res.data.msg);
              }
            });
          }
        }
      });
    },
    check(flag) {
      this.flag = flag;
      this.login();
    },
  },
};
</script>

<style lang="less" scoped>
.register {
  height: 100vh;
  background-image: linear-gradient(to bottom right, #fc466b, #3f5efb);
  overflow: hidden;
}
</style>